
Template.body.addContent((function() {
  var view = this;
  return [ Spacebars.include(view.lookupTemplate("nav")), "\n    ", Spacebars.include(view.lookupTemplate("container")), "\n    ", Spacebars.include(view.lookupTemplate("footer")) ];
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("nav");
Template["nav"] = new Template("Template.nav", (function() {
  var view = this;
  return HTML.DIV({
    "class": "navbar navbar-inverse navbar-fixed-top"
  }, "\n        ", HTML.DIV({
    "class": "navbar-inner"
  }, "\n            ", HTML.DIV({
    "class": "container"
  }, "\n                ", HTML.Raw('<a class="btn btn-navbar" data-toggle="collapse" data-target=".nav-collapse">\n                    <span class="icon-bar"></span>\n                    <span class="icon-bar"></span>\n                    <span class="icon-bar"></span>\n                </a>'), "\n                ", HTML.Raw('<a href="/" class="brand">新闻爆料</a>'), "\n                ", HTML.DIV({
    "class": "nav-collapse"
  }, "\n                    ", HTML.UL({
    "class": "nav"
  }, "\n                        ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "lists"));
    }
  }, HTML.Raw('<a href="/lists" style="font-weight: bold;color:#eee;font-size: 18px;">排行榜</a>')), "\n                        ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "index"));
    }
  }, HTML.Raw('<a href="/">首页</a>')), "\n                        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ "\n                            ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "self"));
      }
    }, HTML.A({
      href: "/self"
    }, Blaze.View("lookup:currentUser.username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("currentUser"), "username"));
    }))), "\n                            ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "friend"));
      }
    }, HTML.A({
      href: "/friend"
    }, "好友")), "\n                            ", HTML.LI(HTML.A({
      href: "/logout"
    }, "登出")), "\n                        " ];
  }, function() {
    return [ "\n                            ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "login"));
      }
    }, HTML.A({
      href: "/login"
    }, "登入")), "\n                            ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("active"), "reg"));
      }
    }, HTML.A({
      href: "/reg"
    }, "注册")), "\n                        " ];
  }), "\n                    "), "\n                "), "\n            "), "\n        "), "\n    ");
}));

Template.__checkName("container");
Template["container"] = new Template("Template.container", (function() {
  var view = this;
  return HTML.DIV({
    id: "container",
    "class": "container"
  }, "\n        ", Spacebars.include(view.lookupTemplate("info")), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "index"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("index")), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "login"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("login")), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "reg"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("reg")), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "self"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("self")), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "friend"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("friend")), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUrl"), "lists"));
  }, function() {
    return [ "\n            ", Spacebars.include(view.lookupTemplate("lists")), "\n        " ];
  }), "\n    ");
}));

Template.__checkName("footer");
Template["footer"] = new Template("Template.footer", (function() {
  var view = this;
  return HTML.Raw('<p class="muted text-center">Newsplatform 2016</p>');
}));

Template.__checkName("info");
Template["info"] = new Template("Template.info", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("info"), "success"));
  }, function() {
    return [ "\n        ", HTML.DIV({
      "class": "alert alert-success"
    }, Blaze.View("lookup:info.success", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("info"), "success"));
    })), "\n    " ];
  }), "\n    ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("info"), "error"));
  }, function() {
    return [ "\n        ", HTML.DIV({
      "class": "alert alert-error"
    }, Blaze.View("lookup:info.error", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("info"), "error"));
    })), "\n    " ];
  }) ];
}));
