
Template.__checkName("friend");
Template["friend"] = new Template("Template.friend", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("friend"));
  }, function() {
    return [ "\n        ", HTML.LI(HTML.SPAN("第", Blaze.View("lookup:rank", function() {
      return Spacebars.mustache(view.lookup("rank"));
    }), "名"), HTML.SPAN(Blaze.View("lookup:user.username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("user"), "username"));
    })), HTML.SPAN("总分是", Blaze.View("lookup:totalScore", function() {
      return Spacebars.mustache(view.lookup("totalScore"));
    }))), "\n    " ];
  });
}));
